@include body-class {

	// Necessary styling for the dialog to work.
	.sui-dialog[aria-hidden="true"] {
		display: none;
	}

	.sui-dialog {
		position: fixed;
		z-index: 1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 160px;
		display: flex;
		align-items: center;
		overflow: auto;

		// Modal sizes.
		&.sui-dialog-sm {

			.sui-dialog-content {
				max-width: calc(400px + (#{$sui-gutter}*2));

				@media (max-width: 400px) {
					max-width: calc(400px + (#{$sui-gutter-md}*2));
				}
			}

			.sui-box-header {
				border: none;
				text-align: center;
				display: block;
				position: relative;
				padding-top: #{$sui-gutter-md * 2};
				padding-bottom: #{$sui-gutter-md / 2};

				.sui-dialog-close {
					width: 30px;
					height: 30px;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					position: absolute;
					top: #{$sui-gutter/2};
					right: #{$sui-gutter/2};
					margin: 0;
					padding: 0;

					&, &:before {
						color: palette( gray, light );
					}

					&:before {
						font-size: 16px;
					}

					&:hover, &:focus {
						outline: none;
					}

					@include media( max-width, md ) {
						top: #{$sui-gutter-md};
						right: #{$sui-gutter-md/2};
					}
				}

				.sui-dialog-image {
					width: 70px;
					height: 70px;
					display: block;
					position: absolute;
					top: -35px;
					left: 50%;
					margin-left: -35px;
					border: 5px solid $white;
					border-radius: 4px;
					background-color: $white;

					img {
						display: block;
						border-radius: 3px;
					}

					@include media( max-width, md ) {
						display: none;
					}
				}

				.sui-box-title {
					margin: 0;
					color: palette( gray, dark );
					font: bold 22px/30px $font--alt;
					letter-spacing: 0px;
				}

				p {
					margin: 10px 0 0;
					color: palette(gray, default);
					font-size: 13px;
					line-height: 22px;

					&:first-child {
						margin-top: 0;
					}
				}

				&.sui-dialog-with-image {
					margin-top: 35px;
					padding-top: #{$sui-gutter * 2};

					@include media( max-width, md ) {
						margin-top: 0;
					}
				}
			}

			.sui-box-body {
				padding-top: #{$sui-gutter-md / 2};
				padding-bottom: #{$sui-gutter / 2};
				text-align: center;

				p {
					color: palette(gray, light);
					font-size: 13px;
					line-height: 20px;
				}

				label {
					text-align: left;
				}

				.sui-label {
					line-height: 22px;
				}

				.sui-form-field {
					margin-bottom: $sui-gutter-md;

					&:last-child {
						margin-bottom: 0;
					}
				}

				&:last-child {
					padding-bottom: #{$sui-gutter-md * 2};
				}

				@include media( max-width, md ) {
					padding-bottom: #{$sui-gutter-md / 2};
				}
			}

			.sui-box-footer {
				align-items: center;
				justify-content: center;
				padding-top: #{$sui-gutter / 2};
				padding-bottom: #{$sui-gutter-md * 2};
				border: 0;

				&.sui-align-unset {
					justify-content: unset;
				}

				@include media( max-width, md ) {
					padding-top: #{$sui-gutter-md / 2};
				}
			}
		}

		&.sui-dialog-lg {
			
			.sui-dialog-content {
				max-width: calc(980px + (#{$sui-gutter}*2));

				@media (max-width: 1200px) {
					max-width: calc(980px + (#{sui-gutter-md}*2));
				}
			}
		}

		@media (max-width: 960px) {
			left: 36px;
		}

		@media (max-width: 782px) {
			left: 0;
		}

		& * {
			box-sizing: border-box;
		}
	}

	.sui-dialog-overlay {
		position: fixed;
		z-index: 2;
		background-color: $modal-overlay-bg-color;
		top: 0;
		right: 0;
		bottom: 0;
		left: 160px;

		@media (max-width: 960px) {
			left: 36px;
		}

		@media (max-width: 782px) {
			left: 0;
		}
	}

	.sui-dialog-content {
		width: 100%;
		max-width: calc(600px + (#{$sui-gutter}*2));
		max-height: 85%;
		position: relative;
		z-index: 2;
		margin: 0 auto;
		padding: 0 $sui-gutter;

		> .sui-box {
			margin: $sui-gutter 0;
			box-shadow: 0 10px 40px $modal-box-shadow-color;

			@media (max-width: 782px) {
				margin: $sui-gutter-md 0;
			}
		}

		.sui-box-body {
			
			& > * {
				margin-bottom: 30px;
				@include media( max-width, md ) {
					margin-bottom: $sui-gutter-md;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		@media (max-width: 782px) {
			max-width: calc(600px + (#{$sui-gutter-md}*2));
			padding: 0 $sui-gutter-md;
		}
	}

	.sui-dialog-close {
		font-size: 1.25em;
		background-image: none;
		background-color: transparent;
		border: 0;
		cursor: pointer;
		color: $modal-close-color;
		@include icon(before, 'close');
		outline-style: none;
	}

	// Folded sidebar
	&.wp-admin.folded {
		
		.sui-dialog {
			left: 36px;

			@media (max-width: 782px) {
				left: 0;
			}
		}

		.sui-dialog-overlay {
			left: 36px;

			@media (max-width: 782px) {
				left: 0;
			}
		}
	}
}

html.sui-has-overlay {
	overflow: hidden;
}


