@include body-class {

	.sui-table {
		width: 100%;
		table-layout: fixed;
		margin: $sui-gutter 0;
		border: 1px solid $table--border-color;
		border-radius: $border-radius;
		//border-collapse: collapse;
		border-spacing: unset;
		border-radius: 5px;
		color: $table--text-color;
		font-size: 13px;

		th, td {
			padding: calc(#{$sui-gutter}/2 - 1px) 5px;
			border-bottom: 1px solid $table--border-color;

			&:first-child {
				padding-left: calc(#{$sui-gutter} - 1px);
	
				@include media( max-width, md ) {
					padding-left: 0;
				}
			}
	
			&:last-child {
				padding-right: calc(#{$sui-gutter} - 1px);
	
				@include media( max-width, md ) {
					padding-right: 0;
				}
			}

			@include media( max-width, md ) {
				padding: calc(#{$sui-gutter-md}/2 - 1px) 5px;
			}
		}

		th {
			white-space: nowrap;
			text-align: left;
			font-weight: 500;
			line-height: 1;
			color: $table--th-color;
		}

		tr {

			&:last-child {

				td {
					border-bottom: 0;
				}
			}
		}

		tfoot {

			th {
				border-top: 1px solid $table--border-color;
				border-bottom: none;
			}
		}

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		@include media( max-width, md ) {
			margin: $sui-gutter-md 0;
			border: 0;
		}
	}

	.sui-table-title {
		margin: 5px 0;
		color: $table--title-color;
		font: bold 13px/22px $font;
		letter-spacing: 0px;

		+ .sui-table {
			margin-top: 0;

			@include media( max-width, md ) {
				margin-top: 0;
			}
		}
	}

	.sui-field-list {
		border: 1px solid $table--border-color;
		border-radius: 5px;
	}

	.sui-field-list-header {
		padding: 20px 30px;
	}

	.sui-field-list-item {
		display: flex;
		align-items: center;
		border-top: 1px solid $table--border-color;
		padding: 15px 30px;
	}

	.sui-field-list-item-label {
		margin-left: 0;
		margin-right: auto;
	}

	.sui-field-list-title {
		font-family: $font;
		font-size: 13px;
		line-height: 30px;
		color: $table--field-list-title-color;
		text-transform: capitalize;
		font-weight: 500;
		margin: 0;
	}

}



